@font-face {
  font-family: 'Droid Sans';
  font-style: normal;
  font-weight: 700;
  src: local('Droid Sans Bold'), local('DroidSans-Bold'), url(https://fonts.gstatic.com/s/droidsans/v6/EFpQQyG9GqCrobXxL-KRMTvw3iLehEZ8Hqbhfrhp4xo.woff2) format('woff2');
  unicode-range: U0 0FF, U131, U152 -153, U2C6, U2DA, U2DC, U2000 -206F, U2074, U20AC, U2212, U2215, UE0FF, UEFFD, UF000; }

@font-face {
  font-family: 'Droid Serif';
  font-style: normal;
  font-weight: 400;
  src: local('Droid Serif'), local('DroidSerif'), url(https://fonts.gstatic.com/s/droidserif/v6/0AKsP294HTD-nvJgucYTaOL2WfuF7Qc3ANwCvwl0TnA.woff2) format('woff2');
  unicode-range: U0 0FF, U131, U152 -153, U2C6, U2DA, U2DC, U2000 -206F, U2074, U20AC, U2212, U2215, UE0FF, UEFFD, UF000; }

/**
 * Usage:
    @include breakpoint(small) {
        width: 100%;
    }
*/
body {
  font-family: 'Droid Serif', Georgia, 'Cambria', 'Times New Roman', Times, serif;
  background-color: #fff;
  font-size: 19px;
  color: #333;
  padding: 0;
  margin: 0;
  width: 100%;
  text-align: center;
  overflow-x: hidden;
  line-height: 1.7; }
  @media (max-width: 600px) {
    body {
      font-size: 16px; } }

a {
  font-weight: bolder;
  color: #3399FF;
  transition: background-color 0.3s ease, color 0.3s ease; }
  a:hover {
    color: #0059b3; }
  a:visited {
    color: #9A3334; }
    a:visited:hover {
      color: #3a1314; }

h1, h2, h3, h4, h5, h6 {
  font-family: 'Droid Sans', 'Lucida Sans Unicode', 'Lucida Sans', Geneva, Verdana, sans-serif; }

hr {
  margin: 1em; }

.page {
  padding: 1em;
  width: 100%;
  max-width: 800px;
  margin: 1em auto 1em auto;
  text-align: left;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box; }
  @media (max-width: 600px) {
    .page {
      margin: 0 auto 0 auto; } }

.footer-date {
  font-size: smaller;
  font-weight: lighter;
  color: #595959; }

article {
  line-height: 2;
  font-family: 'Droid Serif', Georgia, 'Cambria', 'Times New Roman', Times, serif; }

.nav {
  width: 100%;
  display: table;
  table-layout: fixed;
  background: #fff;
  padding: 0;
  list-style-type: none;
  margin: 0; }

.nav-item {
  display: table-cell;
  text-align: center; }
  .nav-item:nth-child(3n+1) > a.nav-link {
    background-color: #fdad8c; }
    .nav-item:nth-child(3n+1) > a.nav-link:hover {
      background-color: #fc8a5a; }
  .nav-item:nth-child(3n+2) > a.nav-link {
    background-color: #89ceba; }
    .nav-item:nth-child(3n+2) > a.nav-link:hover {
      background-color: #65bfa5; }
  .nav-item:nth-child(3n+3) > a {
    background-color: #a490c3; }
    .nav-item:nth-child(3n+3) > a:hover {
      background-color: #896fb1; }

.nav-link {
  display: block;
  padding: 0.5em 0;
  color: #fff;
  text-decoration: none;
  font-size: 123%;
  font-family: 'Droid Sans', 'Lucida Sans Unicode', 'Lucida Sans', Geneva, Verdana, sans-serif;
  font-weight: bolder; }
  .nav-link:visited {
    color: #fff; }
  .nav-link:hover {
    color: #333; }
  @media (max-width: 600px) {
    .nav-link {
      font-size: 100%; } }

.site-title {
  font-size: 200%;
  margin: 0;
  text-align: center; }
  .site-title > a {
    text-decoration: none; }
    .site-title > a:visited {
      color: #fff; }
  @media (max-width: 600px) {
    .site-title {
      font-size: 150%; } }

.post-date {
  font-weight: bolder;
  color: #666666; }

.post-list {
  list-style-type: none;
  padding: 0; }

.post-list-link {
  font-weight: bolder;
  font-size: 120%;
  margin-right: 1em; }

.post-link-meta {
  color: #666666; }

.post-title {
  font-size: 35px; }
  @media (max-width: 600px) {
    .post-title {
      font-size: 28px; } }

.project-img {
  max-width: 100%;
  border: 1px outset #ccc;
  margin: 0.5em auto; }

.header {
  background-color: #3399FF;
  color: #fff;
  padding: 1.5em; }
  @media (max-width: 600px) {
    .header {
      padding: 1em; } }

